import { Route } from '@/types';
import cache from '@/utils/cache';
import got from '@/utils/got';
import { load } from 'cheerio';
import { parseDate } from '@/utils/parse-date';
import timezone from '@/utils/timezone';

const host = 'https://www.nowcoder.com';

export const route: Route = {
    path: '/experience/:tagId',
    categories: ['bbs'],
    example: '/nowcoder/experience/639?order=3&companyId=665&phaseId=0',
    parameters: { tagId: 'ËÅå‰Ωçid [üîóÊü•ËØ¢ÈìæÊé•](https://www.nowcoder.com/profile/all-jobs)Â§çÂà∂ÊâìÂºÄ' },
    features: {
        requireConfig: false,
        requirePuppeteer: false,
        antiCrawler: false,
        supportBT: false,
        supportPodcast: false,
        supportScihub: false,
    },
    radar: [
        {
            source: ['nowcoder.com/'],
            target: '/experience',
        },
    ],
    name: 'Èù¢Áªè',
    maintainers: ['huyyi'],
    handler,
    url: 'nowcoder.com/',
    description: `ÂèØÈÄâÂèÇÊï∞Ôºö

  -   companyIdÔºöÂÖ¨Âè∏ idÔºå[üîóÊü•ËØ¢ÈìæÊé•](https://www.nowcoder.com/discuss/tag/exp), Â§çÂà∂ÊâìÂºÄ
  -   orderÔºö3 - ÊúÄÊñ∞Ôºõ1 - ÊúÄÁÉ≠
  -   phaseIdÔºö0 - ÊâÄÊúâÔºõ1 - Ê†°ÊãõÔºõ2 - ÂÆû‰π†Ôºõ3 - Á§æÊãõ`,
};

async function handler(ctx) {
    const params = new URLSearchParams(ctx.req.query());
    params.append('tagId', ctx.req.param('tagId'));

    const link = new URL('/discuss/experience/json', host);

    // const link = `https://www.nowcoder.com/discuss/experience/json?tagId=${tagId}&order=${order}&companyId=${companyId}&phaseId=${phaseId}`;
    link.search = params;
    const response = await got.get(link.toString());
    const data = response.data.data;

    const list = data.discussPosts.map((x) => {
        const info = {
            title: x.postTitle,
            link: new URL('discuss/' + x.postId, host).href,
            author: x.author,
            pubDate: timezone(parseDate(x.createTime), +8),
            category: x.postTypeName,
        };
        return info;
    });

    const out = await Promise.all(
        list.map((info) =>
            cache.tryGet(info.link, async () => {
                const response = await got.get(info.link);
                const $ = load(response.data);

                info.description = $('.nc-post-content').html();

                return info;
            })
        )
    );

    return {
        title: `ÁâõÂÆ¢Èù¢ÁªèTag${ctx.req.param('tagId')}`,
        link: link.href,
        item: out,
    };
}
